// アイコン
//
// ページ内に使われているアイコンについて。<br>
// アイコン以外にアイコンフォントについてもココで記載
//
// Markup:
// <ul class="list-disc">
//   <li>NEWアイコン（一部で必須アイコンとしても使用）.ico-new
//     <p><span class="ico-new">NEW</span></p></li>
//   <li>外部リンクアイコン.ico-external
//     <p><a href="javascript:;">外部リンク<img src="/img/icon/ico_external_link.svg" width="16" height="14" alt="" class="ico-external"></a></p></li>
//   <li>矢印アイコン.ico-arw-wedge
//     <p><a href="javascript:;" class="ico-arw-wedge">矢印アイコン</a></p></li>
//   <li>矢印アイコン(アイコンフォント使用の余白・縦位置調整).ico-arrow
//     <p><a href="javascript:;"><span class="ficon-keyboard_arrow_right ico-arrow"></span>矢印アイコン</a></p></li>
//   <li>アイコンフォント
//     <p><span class="ficon-arrow_back"></span>左向き矢印.ficon-arrow_back</p>
//     <p><span class="ficon-arrow_forward"></span>右向き矢印.ficon-arrow_forward</p>
//     <p><span class="ficon-arrow_upward"></span>上向き矢印.ficon-arrow_upward</p>
//     <p><span class="ficon-arrow_downward"></span>下向き矢印.ficon-arrow_downward</p>
//     <p><span class="ficon-keyboard_arrow_left"></span>左向き矢印（棒状）.ficon-keyboard_arrow_left</p>
//     <p><span class="ficon-keyboard_arrow_right"></span>右向き矢印（棒状）.ficon-keyboard_arrow_right</p>
//     <p><span class="ficon-keyboard_arrow_up"></span>上向き矢印（棒状）.ficon-keyboard_arrow_up</p>
//     <p><span class="ficon-keyboard_arrow_down"></span>下向き矢印（棒状）.ficon-keyboard_arrow_down</p>
//     <p><span class="ficon-desktop_windows"></span>PCアイコン.ficon-desktop_windows</p>
//     <p><span class="ficon-phone_iphone"></span>SPアイコン.ficon-phone_iphone</p>
//     <p><span class="ficon-search"></span>虫眼鏡アイコン.ficon-search</p>
//     <p><span class="ficon-star"></span>星アイコン（塗り）.ficon-star</p>
//     <p><span class="ficon-star_border"></span>星アイコン（空）.ficon-star_border</p>
//     <p><span class="ficon-star_half"></span>星アイコン（半分）.ficon-star_half</p>
//     <p><span class="ficon-home"></span>ホームアイコン.ficon-home</p></li>
// </ul>
//
// Styleguide 2.0.0

//------------------------------------------------------------
// icons
//------------------------------------------------------------

//=========================
// 各種アイコン
//=========================

// タグ
//------------------------------------------------------------

.ico-new {
	@include inline-block;
	margin: 0 0 0 2px;
	padding: 2px 5px;
	background-color: colors(col-red);
	color: colors(col-white);
	@include fz(10);
	line-height: 1;
	vertical-align: middle;
	white-space: nowrap;
}

.ico-external {
	@include inline-block;
	@include margin(null 5px null 5px);
	vertical-align: baseline;
}

// やじるし
//------------------------------------------------------------

.ico-arrow {
	color: colors(col-gray);
	text-decoration: none;
	vertical-align: middle;
}

.ico-arw-wedge {
	@include inline-block;
	padding-left: 15px;
	position: relative;
	&:after {
		@include arw-wedge;
		top: 0.75em;
		left: 0;
		content: "";
	}
}

//=========================
// アイコンサイズ調整
//=========================

@for $value from 1 through 10 {
	.ico-size-#{$value * 1} {
		@include scale($value * 0.1, $value * 0.1);
	}
}

//=========================
// アイコンフォント指定
//=========================

@font-face {
	font-family: 'kcvicon';
	src:
		url('/img/fonts/kcvicon.ttf?w4umya') format('truetype'),
		url('/img/fonts/kcvicon.woff?w4umya') format('woff'),
		url('/img/fonts/kcvicon.svg?w4umya#kcvicon') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^="ficon-"], [class*=" ficon-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	display: inline-block;
	font-family: 'kcvicon' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

