//------------------------------------------------------------
// bourbon
//------------------------------------------------------------
@import 'bourbon';

//------------------------------------------------------------
// color variavle
//------------------------------------------------------------
@import '../common/color';

//------------------------------------------------------------
// mixin
//------------------------------------------------------------
@import '../common/mixin';

//------------------------------------------------------------
// Components
//------------------------------------------------------------

//=========================
// base
//=========================
@import '../common/components/base';

//=========================
// icons
//=========================
@import '../common/components/icons';

//------------------------------------------------------------
// page layout
//------------------------------------------------------------

.error-content {
	width: 700px;
	@include margin(80px auto null auto);
	@include break-point(tbportrait) {
		width: 100%;
		@include padding(null 20px null 20px);
		box-sizing: border-box;
	};
	@include break-point(mobile) {
		@include margin(30px auto null auto);
	};
}

.maintenance-txt-block {
	@include margin(20px null 20px null);
	@include padding(20px null 20px null);
	border-top: 1px solid colors(col-lightgray);
	border-bottom: 1px solid colors(col-lightgray);
}

.maintenance-time {
	color: colors(col-red);
	@include fz(28);
	font-weight: 700;
	@include break-point(mobile) {
		@include fz(20);
	};
	br {
		display: none;
		@include break-point(mobile) {
			display: inline;
		};
	}
}

.error-copyright {
	@include margin(50px null null null);
	@include fz(14);
	text-align: center;
	@include break-point(mobile) {
		@include margin(20px null null null);
		@include fz(12);
	};
}

